const screenshotServiceBaseUrl = 'https://pwa-screenshots.azurewebsites.net';
var EndPoints;
(function (EndPoints) {
    EndPoints["colorScheme"] = "getColorScheme";
    EndPoints["base64"] = "screenshotsAsBase64Strings";
    EndPoints["zip"] = "downloadScreenshotsZipFile";
})(EndPoints || (EndPoints = {}));
export async function generateScreenshots(screenshotsList) {
    try {
        const res = await fetch(`${screenshotServiceBaseUrl}/${EndPoints.base64}`, {
            method: 'POST',
            headers: {
                'content-type': 'application/json',
                'Access-Control-Allow-Origin': '*',
            },
            referrerPolicy: 'origin',
            credentials: 'omit',
            body: JSON.stringify({
                url: screenshotsList,
            }),
        });
        if (res.ok) {
            const response = (await res.json());
            let screenshots;
            try {
                //const manifestContext = await fetchOrCreateManifest(); // How to create a constant manifest state across files like this?
                //screenshots = manifestContext.manifest.screenshots || [];
                screenshots = [];
            }
            catch (error) {
                screenshots = [];
            }
            screenshots = screenshots.concat(response.images.map(image => {
                image.src = 'data:image/png;base64,' + image.src;
                return image;
            }));
            /* await updateManifest({
              screenshots,
            }); */
            return screenshots;
        }
        else {
            throw new Error(await res.text());
        }
    }
    catch (e) {
        console.error(e);
    }
    return undefined;
}
