export function resolveUrl(baseUrl, url) {
    let parsedUrl = undefined;
    try {
        if (url) {
            parsedUrl = new URL(url);
        }
    }
    catch (e) {
        if (!(e instanceof TypeError)) {
            console.error(e);
        }
    }
    if (!parsedUrl) {
        try {
            if (baseUrl && url) {
                parsedUrl = new URL(url, baseUrl);
            }
        }
        catch (e) {
            console.error(e);
        }
    }
    if (!parsedUrl && baseUrl) {
        return new URL(baseUrl);
    }
    return parsedUrl;
}
